.footer {
  position: absolute;
  bottom: 20px;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 50px;
  text-align: center;
}

.webStore {
  font-family: Montserrat;
  font-size: 9px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 2.33;
  letter-spacing: 1.53px;
  color: var(--black);
  text-decoration: none;
  transition: color 0.2s ease-out 0.1s;
}

.webStore:hover {
  color: var(--macaroni-and-cheese);
}

/* .icon {
  width: 9px;
  height: 12px;
  fill: var(--macaroni-and-cheese);
} */

.text {
  font-family: Montserrat;
  font-size: 9px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 2.33;
  letter-spacing: 0.77px;
  color: var(--blue-grey);
}

.borders {
  font-family: Montserrat;
  font-size: 9px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 2.33;
  letter-spacing: 0.77px;
  color: var(--blue-grey);
}

@media (min-width: 768px) {
  .webStore {
    font-size: 14px;
  }

  .text {
    font-size: 14px;
  }

  /* .icon {
    width: 14px;
    height: 18px;
  } */

  .footer {
    flex-direction: row;
    justify-content: flex-end;
    padding-top: 10px;
    padding-right: 30px;
    align-self: flex-end;
    flex: 0 0 100%;
    bottom: 0;
    right: 0;
  }

  .borders {
    height: 30px;
    font-size: 14px;
    border-left: 1px solid #858598;
    border-right: 1px solid #858598;
    margin: 0 5px;
    padding: 0 5px;
  }
}
